import{getSettings,changeSetting}from'soapbox/actions/settings';var getPinnedHosts=function getPinnedHosts(state){var settings=getSettings(state);return settings.getIn(['remote_timeline','pinnedHosts']);};var pinHost=function pinHost(host){return function(dispatch,getState){var state=getState();var pinnedHosts=getPinnedHosts(state);return dispatch(changeSetting(['remote_timeline','pinnedHosts'],pinnedHosts.add(host)));};};var unpinHost=function unpinHost(host){return function(dispatch,getState){var state=getState();var pinnedHosts=getPinnedHosts(state);return dispatch(changeSetting(['remote_timeline','pinnedHosts'],pinnedHosts.remove(host)));};};export{pinHost,unpinHost};